				var This=this;
				var myChart1 = echarts.init(document.getElementById('tabEachOneId'));
				var myChart2 = echarts.init(document.getElementById('tabEachTwoId'));
				var myChart3 = echarts.init(document.getElementById('tabEachThreeId'));

				// 指定图表的配置项和数据
				

				function eachZong(obj, obj2,min,max) {
					
					
					var option = $.extend(true, option, This.eachData);

					option.series = [{
						name: '',
						type: 'line',
						stack: '总量',
						data: []
					}]
					option.legend.data = [''];
					
					
					
					
					option.xAxis.data=[];
					option.series[0].data=[];
					
					for(var i = 0, j = obj.length - 1; i < obj.length; i++, j--) {
						
						var changeTime=obj[j].indexDate;
						var arrTime=changeTime.split('-');
						changeTime=arrTime[1]+'/'+arrTime[2];
						
						option.xAxis.data.push(changeTime);    //  x 轴
						option.series[0].data.push(obj[j].smeiValue);    // y 轴
					}
					option.yAxis.min=min;
					option.yAxis.max=max;
					obj2.setOption(option);
				}
				// 根据数据  生成 图表
				indexRrData({}).then((res) => {

