				var year1=date1.getFullYear(); 
				var month1=date1.getMonth()+1;
				var day1 = date1.getDate();
				month1 =(month1<10 ? "0"+month1:month1); 
				var dateSp = (year1.toString()+month1.toString()+day1);
				
				var parm = {
				 	id:"2670",
				 	sp:dateSp,
				 	ep:dateEp 	
				}
				var valuess = [];
				var categoryData = [];
				var volumes =[];
				Kecharts(parm).then(res =>{
				 	res.data.bar.forEach((o)=>{
				 		var values = [];
				 		values.push(o.open,o.close,o.low,o.high)
				 		valuess.push(values)
				 		categoryData.push(o.date.toString().substr(0,4)+'-' + o.date.toString().substr(4,2) + '-' + o.date.toString().substr(6,2));
						volumes.push(o.vol)	
						
				 	})
				 	option12.xAxis[0].data= categoryData;
			 		option12.xAxis[1].data= categoryData;
			 		option12.series[1].data = volumes;
					option12.series[0].data = valuess;
					myChart.setOption(option12); 
				 	
				})
			},
			content(){
				
				if(this.$own.getHref('channelName')){
				
					this.channelName=decodeURIComponent(this.$own.getHref('channelName'));
					this.channelCode=decodeURIComponent(this.$own.getHref('channelCode'));
					if(this.channelName == '海运新闻'){
						this.channelName = '海运'
					}
					if(this.channelCode == 'HYXW_EN'){
						this.channelMDesc = 'YYYW'
					}else if(this.channelCode == 'YLSC_EN'){
						this.channelMDesc = 'YYYW'
					}
					
				}else{
					this.channelName=window.localStorage.getItem('channelName');
					this.channelCode=window.localStorage.getItem('channelCode');
					if(this.channelName == '海运新闻'){
						this.channelName = '海运'
					}
				}
				
			},
			
			tabBu(){
				this.num=0
			},
			tabW(){
				this.num=1
			},
			getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					
					this.imgUrlBanner = res.datas[0].imageUrl
					
				});
			}
		},
		mounted(){
			this.content();
			this.tabclick();
			this.postData();
			this.rightFn();
			this.getOilData();
			this.eachatsFn(); //布伦特K线图
			this.eachatsFn1() //WTI线图
			this.getBanner()
		}
	}

</script>

<style>
.cont-left-pic{
		text-align: center;
	}
.divUl{ display: none;}
.inf-cont-right .tabYse .tabNone{ display: none;}
.yybg-li-tr{ position:absolute;right:0;top:-2px;color:#f00;font-size:24px; font-weight:normal;}
.yybg-li-tg{ position:absolute;right:0;top:-2px;color:#00cc00;font-size:24px; font-weight:normal;}
.yybg-li-tjr{color:#f00;width:calc(100% - 15px);padding-left:15px;}
.cont-left-txt{width: 100% !important;}
.cont-left-txt dt a{font-size: 16px !important;}
.cont-left-con{padding:10px !important;}

</style>


